}
.cart-list .cart-item {
  flex: 1 1;
  background: #fff;
  margin-right: 15px;
  display: flex;
  align-items: center;
  height: 90px;
}
.cart-list .cart-item-icon {
  height: 90px;
  line-height: 90px;
  text-align: center;
  color: #fff;
  font-size: 34px;
  flex: 0 0 90px;
}
.cart-list .cart-item:last-child {
